body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Quicksand';

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  height: 100vh; /* Set the body height to 100% of the viewport height */
  /*overflow-x: hidden; /* Prevent scrollbars from appearing */
}

h1, h2, h3 {
  font-family: 'Nunito', sans-serif;
}


p{
  font-family:'Quicksand';
  font-size:1.4rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  /*color pallet*/
  --lightCream:#D9D9D9;
  --coral:#bec6c7;
  --darkGrey:#353535;
  --white:#FFFFFF;
  
  /*
  --lightCream:#FFF6DC;
  --coral:#ffc6ac;
   --darkGrey:#202228;
  */
  
  --darkCoral:#9c502d;
  --lime:#32CD32;
  --blue:#284B63;
  --grey:#808080;
  --gold:#FFD700;
  --lightPeach:#FFC6AC;
  --coral2:#FF7F50 ;
  --aquamarine:#ABFFE4;
}

:root{
  --main-background:var(--lightCream);
  --accent-background:var(--coral);
  --text:var(--blue);
  --landingpage-title:var(--darkGrey);
  --landingpage-nameplate:var(--darkGrey);
  --projectTitle:var(--darkGrey);
  --projectSubject:var(--white);
  --projectText:var(--darkGrey);
  --contentblock-background:var(--coral);
  --contentblock-fade:var(--coral);
  --contentBorder:var(--coral);
}
